<template>
  <div class="useruser">
    <el-main>
      <div class="top">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <div class="main">
        <div class="search">
          <el-form :inline="true" :model="pagelistquery" class="demo-form-inline">
            <el-form-item>
              <el-input v-model="pagelistquery.user" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-select v-model="pagelistquery.realstate" placeholder="">
                <el-option label="" value></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="-1"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-select v-model="pagelistquery.companystate" placeholder="">
                <el-option label="" value></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="-1"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getuserlist"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <el-table
          v-loading="loading"
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          :data="tableData"
          border
          style="width: 100%;min-height:500px"
        >
          <el-table-column prop="username" label=""></el-table-column>
          <el-table-column prop="nickname" label=""></el-table-column>
          <el-table-column prop="realstate" label="">
            <template slot-scope="scope">
              <el-button
                type="text"
                @click="changerealstatedialog(scope.row)"
              >{{ scope.row.realstate |userstatefilter}}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="companystate" label="">
            <template slot-scope="scope">
              <el-button
                type="text"
                @click="changecompanystatedialog(scope.row)"
              >{{ scope.row.companystate |userstatefilter}}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="activationdate" label="">
            <template slot-scope="scope">{{ scope.row.activationdate | dataFormat }}</template>
          </el-table-column>
          <el-table-column fixed="right" label="" width="170">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                :disabled="uinfo.username !== 'admin'"
                @click="activationdate(scope.row)"
              ></el-button>
              <el-button
                type="text"
                size="small"
                :disabled="uinfo.username !== 'admin'"
                @click="deleteuser(scope.row)"
              ></el-button>
              <el-button
                type="text"
                size="small"
                :disabled="scope.row.username== 'admin'||uinfo.username !== 'admin'"
                @click="changepw(scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" :visible.sync="dialogpw" width="500px">
          <el-form :model="changepassword">
            <el-form-item label="" label-width="100px">{{changepassword.username}}</el-form-item>
            <el-form-item label="" label-width="100px">
              <el-input v-model="changepassword.newpassword" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogpw = false"> </el-button>
            <el-button type="primary" @click="change"> </el-button>
          </div>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="dialogcompany">
          <div v-if="changecompanystateuser.companystate===1">
            <img src="../../assets/img/noinfo.png" width="100%" alt />
            <div style="width:100%;text-align:center"></div>
          </div>
          <el-form v-else>
            <el-form-item label="">{{changecompanystateuser.companyname}}</el-form-item>
            <el-form-item label="">
              <img
                v-for="(img,id) in changecompanystateuser.companyimg"
                style="width:40% ;margin:20px"
                :key="id"
                :src="img.url"
                alt
              />
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer" v-if="changecompanystateuser.companystate===1">
            <el-button @click="dialogcompany = false"></el-button>
          </div>
          <div slot="footer" v-else class="dialog-footer">
            <el-button
              type="primary"
              v-if="changecompanystateuser.companystate!==3"
              @click="changestate('companystate',3,changecompanystateuser.user_id,changecompanystateuser.companyname)"
            ></el-button>
            <el-button
              type="danger"
              @click="changestate('companystate',0,changecompanystateuser.user_id)"
            ></el-button>
            <el-button @click="dialogcompany = false"></el-button>
          </div>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="dialogstudent">
          <div v-if="changerealstateuser.realstate===1">
            <img src="../../assets/img/noinfo.png" width="100%" alt />
            <div style="width:100%;text-align:center"></div>
          </div>
          <el-form v-else>
            <el-form-item label="">{{changerealstateuser.realname}}</el-form-item>
            <el-form-item label="">{{changerealstateuser.studentid}}</el-form-item>
            <el-form-item label="">
              <img
                v-for="(img,id) in changerealstateuser.studentcard"
                style="width:40% ;margin:20px"
                :key="id"
                :src="img.url"
                alt
              />
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer" v-if="changerealstateuser.realstate===1">
            <el-button @click="dialogstudent = false"></el-button>
          </div>
          <div slot="footer" v-else class="dialog-footer">
            <el-button
              type="primary"
              v-if="changerealstateuser.realstate!==3"
              @click="changestate('realstate',3,changerealstateuser.user_id)"
            ></el-button>
            <el-button
              type="danger"
              @click="changestate('realstate',0,changerealstateuser.user_id)"
            ></el-button>
            <el-button @click="dialogstudent = false"></el-button>
          </div>
        </el-dialog>
        <el-dialog title="()" :visible.sync="activationttime.dialog" width="500px">
          <el-form :model="activationttime">
            <el-form-item label>
              <el-input v-model="activationttime.time"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="activationttime.dialog = false"> </el-button>
            <el-button type="primary" @click="changeactivationdate"> </el-button>
          </div>
        </el-dialog>
        <!---->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pagelistquery.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pagelistquery.total"
          style="margin-top: 20px;"
        ></el-pagination>
      </div>
    </el-main>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "useruser",
  data() {
    return {
      changepassword: {}, //
      changerealstateuser: {}, //
      changecompanystateuser: {}, //
      dialogstudent: false,
      dialogcompany: false,
      dialogpw: false, //
      loading: false,
      pagelistquery: {
        realstate: "",
        companystate: "",
        total: 0,
        page: 1,
        pagesize: 10,
        user: "",

      },
      tableData: [], //

      activationttime: {
        time: 0,
        userid: "",
        jubao_id: "",
        dialog: false //
      }
    };
  },
  computed: {
    ...mapState({
      uinfo: state => state.user.uinfo
    })
  },
  filters: {
    userstatefilter(state) {
      if (state == 3) {
        return "";
      }
      if (state == 2) {
        return "";
      }
      if (state == 1) {
        return "";
      }
      if (state == 0) {
        return "";
      }
    }
  },
  methods: {
    activationdate(row) {
      this.activationttime.userid = row.user_id;
      this.activationttime.dialog = true;
    },
    //
    async changeactivationdate() {
      this.changepassword.type = "adminuser";
      let res = await this.$axios.post(
        "/admin/changeactivationdate",
        this.qs.stringify(this.activationttime)
      );
      if (res.data.state.type === "SUCCESS") {
        this.$message.success("");
        this.getuserlist();
        this.activationttime.dialog = false;
      }
    },
    async changestate(type, state, userid, companyname) {
      let data = {
        type: type,
        state: state,
        user_id: userid,
        company_name: companyname
      };
      let res = await this.$axios.post(
        "/admin/changeuserstate",
        this.qs.stringify(data)
      );
      if (res.data.state.type === "SUCCESS") {
        this.$message.success("");
        this.getuserlist();
        this.dialogstudent = false;
        this.dialogcompany = false;
      }
    },
    changepw(row) {
      console.log(111);
      this.dialogpw = true;
      this.changepassword = row;
    },
    //
    async change() {
      this.changepassword.type = "adminuser";
      let res = await this.$axios.post(
        "/admin/changepassword",
        this.qs.stringify(this.changepassword)
      );
      if (res.data.state.type === "SUCCESS") {
        this.$message.success("");
        this.dialogpw = false;
      }
    },
    //

    async deleteuser(row) {
      let data = {
        user_id: row.user_id,
        usertype: "user"
      };
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post(
            "/admin/deleteuser",
            this.qs.stringify(data)
          ).then(res => {
            if (res.data.state.type === "SUCCESS") {
              this.$message.success("");
              this.dialogpw = false;
              this.getuserlist();
            }
          })
        })
    },
    //
    async changeuseruserstate(row) {
      console.log(row);
      let res = await this.$axios.post(
        "/admin/changeuseruserstate",
        this.qs.stringify(row)
      );
      if (res.data.state.type === "SUCCESS") {
        this.$message.success("");
      }
    },
    //
    changecompanystatedialog(user) {
      this.changecompanystateuser = JSON.stringify(user);
      this.changecompanystateuser = JSON.parse(this.changecompanystateuser);
      this.changecompanystateuser.companyimg = JSON.parse(
        this.changecompanystateuser.companyimg
      );
      this.dialogcompany = true;
    },
    //
    changerealstatedialog(user) {
      console.log(user);
      this.changerealstateuser = JSON.stringify(user);
      this.changerealstateuser = JSON.parse(this.changerealstateuser);
      this.changerealstateuser.studentcard = JSON.parse(
        this.changerealstateuser.studentcard
      );
      this.dialogstudent = true;
    },

    handleSizeChange(val) {
      this.pagelistquery.pagesize = val;
      this.getuserlist();
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.pagelistquery.page = val;
      this.getuserlist();
      console.log(`: ${val}`);
    },
    async getuserlist() {
      console.log(this.pagelistquery)
      this.loading = true;
      let res = await this.$axios.post(
        "/admin/getuserlist",
        this.qs.stringify(this.pagelistquery)
      );
      if (res.data.state.type === "SUCCESS") {
        this.tableData = res.data.data;
        console.log(res.data);
        this.pagelistquery.total = res.data.count;
        this.loading = false;
      }
    },
    jubao() {
      console.log(1)
      this.pagelistquery.user = this.$route.query.user;
      this.activationttime.jubao_id = this.$route.query.jubao_id;
    }
  },
  created() {
    this.$route.query .user&& this.jubao();
    this.getuserlist();
  }
};
</script>
<style  scoped>
.top {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 35px);
  padding: 12px 16px;
  background-color: #fff;
}
.useruser {
  position: relative;
  width: 100%;
}
.main {
  margin-top: 40px;
  padding: 20px;
  background-color: #fff;
}
</style>
