      <div class="main">
        <div class="user-info clearfix">
          <h4></h4>
          <el-divider></el-divider>
          <ul class="first-userInfo">
            <li class="user-li">
               :
              <span class="user-name" title="726647774@qq.com">{{
                tableData.username
              }}</span>
            </li>
            <li>
               :
              <span>{{ tableData.nickname }}</span>
              <el-button type="text" size="small" @click="name = true"
                ></el-button
              >
              <el-input
                style
                v-if="name"
                v-model="tableData.nickname"
                @keyup.enter.native="changeadminuser()"
              ></el-input>
            </li>
            <li>
               :
              <span>{{ tableData.user_createtime | dataFormat }}</span>
            </li>
          </ul>
          <ul>
            <li>
               :
              <span v-if="tableData.user_state == '1'"></span>
              <span v-else></span>
